/**
 * X marks the spot
 */

var currentMarkIndex = 0
const maxLen = 4

const markPath = '/textures/gg-mark-pink.png'
const markTexture = new THREE.TextureLoader().load( markPath )
markTexture.encoding = THREE.sRGBEncoding

const markPathIn = '/textures/gg-mark.png'
const markTextureIn = new THREE.TextureLoader().load( markPathIn )
markTextureIn.encoding = THREE.sRGBEncoding

const posXs = [-6,-2.5,-8,-16,-14]
const posZs = [-15,-6,2.5,-0.5,-8.5]

const spots = []
const spotsNames = ['gate','rumah','balebale','celengan','gerobak']

for (let s=0; s < posXs.length; s++)
{
    const mark = new THREE.Mesh(
        new THREE.PlaneGeometry( 1.5,1.5 ),
        new THREE.MeshStandardMaterial( { map: markTexture , transparent: true })
    )
    mark.position.set(posXs[s],0.01,posZs[s])
    mark.rotation.x = THREE.Math.degToRad( -90 )
    spots.push(mark)
    scene.add(mark)
}

spots[currentMarkIndex].material.map = markTextureIn

/**
 * Sphere
 */

const sphere = new THREE.Mesh( 
    new THREE.SphereGeometry( 0.5,8,8 ), 
    new THREE.MeshPhongMaterial( { color: '#08f7fe', shininess: 1, reflectivity: 1, transparent: true, opacity: 0.7} ) )
sphere.position.set(posXs[currentMarkIndex],0,posZs[currentMarkIndex])

scene.add( sphere )


