function onKeyDown( event ) {

    switch (event.keyCode)
    {
        case 34: //pagedown key
            
            if (menuIsUp) {
                /*
                scene.fog.color = new THREE.Color('#ffffff')
                scene.fog.far = 80
                scene.remove( explanation )
                explanationIsUp = false

                scene.remove(clues, answerMat)
                */


                switch (currentMarkIndex) {
                    case 0: menus[0].visible = false; clues.visible = false; menuIsUp = false; break
                    case 1: menus[1].visible = false; menuIsUp = false; break
                    case 2: menus[2].visible = false; menuIsUp = false; break
                    case 3: menus[3].visible = false; menuIsUp = false; break
                    case 4: menus[4].visible = false; menuIsUp = false; break
                }
                answer.visible = false

            }

            break

        case 33: //pageup key

            if (!menuIsUp) {

                /*
                scene.fog.color = new THREE.Color('#000000')
                scene.fog.far = 10
                scene.add( explanation )
                expMat.map = expImgPaths[currentMarkIndex]
                explanationIsUp = true

                if (sphereAtGate) {
                    scene.add(clues)
                }
                */

                switch (currentMarkIndex) {
                    case 0: menus[0].visible = true; clues.visible = true; menuIsUp = true; break
                    case 1: menus[1].visible = true; menuIsUp = true; break
                    case 2: menus[2].visible = true; menuIsUp = true; break
                    case 3: menus[3].visible = true; menuIsUp = true; break
                    case 4: menus[4].visible = true; menuIsUp = true; break
                }
            }
            
            break
        
        case 38: //up key
            /*
            if (sphereAtGate && explanationIsUp) {
                if (cluesIndex === clueImgLen) {
                    cluesIndex = 0
                } else {
                    cluesIndex++
                }
            }
            clues.material.map = cluesTextures[cluesIndex]
            */

            if (clues.visible === true) {
                if (cluesIndex === 0) {
                    cluesIndex = clueImgLen
                } else {
                    cluesIndex--
                }
                clues.material.map = cluesTextures[cluesIndex]
            }


            break

        case 40: //down key
            /*
            if (sphereAtGate && explanationIsUp) {
                if (cluesIndex === 0) {
                    cluesIndex = clueImgLen
                } else {
                    cluesIndex--
                }
            }
            clues.material.map = cluesTextures[cluesIndex]
            */
            if (clues.visible === true) {
                if (cluesIndex === clueImgLen) {
                    cluesIndex = 0
                } else {
                    cluesIndex++
                }
                clues.material.map = cluesTextures[cluesIndex]
            }
            
            break

        case 37: //left key
            spots[currentMarkIndex].material.map = markTexture //reset the previous mark color

            /*
            if (explanationIsUp) {
                scene.remove(explanation)
                explanationIsUp = false
                scene.fog.far = 80
                scene.fog.color = new THREE.Color('#ffffff')

                scene.remove(clues, answerMat)
            }
            */
            for (let m of menus) {
                m.visible = false
                menuIsUp = false
            }
            clues.visible = false
            answer.visible = false

            if (currentMarkIndex === 0) {
                currentMarkIndex = maxLen
            } else {
                currentMarkIndex--
            }
            sphere.position.set(posXs[currentMarkIndex],0,posZs[currentMarkIndex])

            spots[currentMarkIndex].material.map = markTextureIn

            //experiment.innerHTML = spotsNames[currentMarkIndex]

            
    
            break

        case 39: //right key
            spots[currentMarkIndex].material.map = markTexture //reset the previous mark color
            
            /*
            if (explanationIsUp) {
                scene.remove(explanation)
                explanationIsUp = false
                scene.fog.far = 80
                scene.fog.color = new THREE.Color('#ffffff')

                scene.remove(clues, answerMat)
            }
            */

            for (let m of menus) {
                m.visible = false
                menuIsUp = false
            }
            clues.visible = false
            answer.visible = false
            
            if (currentMarkIndex === maxLen) {
                currentMarkIndex = 0
            } else {
                currentMarkIndex++
            }
            sphere.position.set(posXs[currentMarkIndex],0,posZs[currentMarkIndex])

            spots[currentMarkIndex].material.map = markTextureIn

            //experiment.innerHTML = spotsNames[currentMarkIndex]

            break

        case 13: //enter key
            
            /*
            if (sphereAtGate && explanationIsUp) {

                if (cluesIndex === 3) {
                    //experiment.innerHTML = 'You got it right! blah blah <br> Now we\'ll move on to the next scene'

                    answerMat.material.map = correctAnswer
                    scene.add(answerMat)
                    answerIsCorrect = true

        
                } else {
                    //experiment.innerHTML = 'This is not right.<br>Please try again'

                    answerMat.material.map = wrongAnswer
                    scene.add(answerMat)
                    answerIsCorrect = false
                }
            }
            */
            if (clues.visible === true) {
                if (cluesIndex === 3) {
                    answer.material.map = correctAnswer
                    answerIsCorrect = true
                } else {
                    answer.material.map = wrongAnswer
                    answerIsCorrect = false
                }
                answer.visible = true
            } 
            
            break
    }

    
}